* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

img {
  display: block;
  max-width: 100%;
}

a {
  color: #141630;
  text-decoration: none;
  transition: color 0.15s ease-in-out;
}
a:hover {
  color: #EF7D3F;
}

html, body {
  font-family: "Work Sans", sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Hanken Grotesk", sans-serif;
}

h1 {
  font-size: 52px;
  line-height: 63px;
}

h2 {
  font-size: 28px;
  line-height: 33px;
}

h3 {
  font-size: 22px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 16px;
}

h6 {
  font-size: 14px;
}

p {
  font-size: 14px;
  line-height: 16px;
}

html, body {
  background: linear-gradient(90deg, #141630 0%, #1F2350 100%);
  color: #FFFFFF;
}

.main {
  display: block;
  margin: 0 auto;
  padding: 40px 20px;
  max-width: 1192px;
}
@media (min-width: 769px) {
  .main {
    padding: 70px;
  }
}

body {
  padding-top: 91px;
}
@media (min-width: 769px) {
  body {
    padding-top: 100px;
  }
}

.header {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
  width: 100%;
  height: 91px;
  padding: 15px 20px;
  background-color: #FFFFFF;
  box-shadow: 0 5px 6px 0 rgba(51, 51, 51, 0.1);
}
@media (min-width: 769px) {
  .header {
    height: 100px;
    padding: 15px 54px;
  }
}
.header .nav {
  width: 100%;
}
.header .nav .logo {
  position: relative;
  z-index: 6;
  width: 50px;
  height: 54px;
}
@media (min-width: 1024px) {
  .header .nav .logo {
    width: 72px;
    height: 72px;
  }
}
.header .nav__start, .header .nav__end {
  display: none;
}
@media (min-width: 1024px) {
  .header .nav__start, .header .nav__end {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
  }
}
.header .nav__end {
  margin-left: auto;
}
.header .nav__mobile {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  width: 100%;
}
@media (min-width: 1024px) {
  .header .nav__mobile {
    display: none;
  }
}
.header .nav__menu-icon {
  margin-left: auto;
  display: block;
  width: 36px;
  height: 36px;
  background: url("../media/mobile_menu_icon.png") no-repeat center/contain;
  background-image: url("../media/mobile_menu_icon.svg");
}
.header .nav__menu-icon--close {
  background: url("../media/mobile_nav_close_icon.png") no-repeat center/contain;
  background-image: url("../media/mobile_nav_close_icon.svg");
}
.header .nav__item {
  display: block;
  margin: 0 28px;
  font-size: 16px;
  line-height: 19px;
  color: #141630;
}
.header .nav__item:hover {
  color: #EF7D3F;
}
.header .nav__item--small {
  font-size: 14px;
  line-height: 23px;
  letter-spacing: 0.72px;
}
.header .nav__item--search {
  background-image: url("../media/header_search_icon.png");
  background-image: url("../media/header_search_icon.svg");
  background-position: left;
  background-size: contain;
  background-repeat: no-repeat;
  padding-left: 30px;
}
.header .nav__item--cart {
  background-image: url("../media/header_cart_icon.png");
  background-image: url("../media/header_cart_icon.svg");
  background-position: left;
  background-size: contain;
  background-repeat: no-repeat;
  padding-left: 30px;
}

.mobile-menu {
  display: none;
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 5;
  background: linear-gradient(90deg, #141630 0%, #1F2350 100%);
}
.mobile-menu__nav {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.mobile-menu__nav ul {
  list-style: none;
  margin: 0 0 63px;
  padding: 0;
}
.mobile-menu__nav ul li {
  margin: 0 0 37px;
  padding: 20px;
}
.mobile-menu__nav ul li:last-child {
  margin-bottom: 0;
}
.mobile-menu__nav ul li a {
  color: #EF7D3F;
  font-size: 20px;
  line-height: 24px;
}
.mobile-menu__nav ul li a:hover {
  color: #FFFFFF;
}
.mobile-menu .header {
  position: relative;
  background-color: transparent;
  box-shadow: none;
}

.footer__container {
  background-color: #FFFFFF;
}
.footer__main {
  display: block;
  margin: 0 auto;
  padding: 32px;
  max-width: 1192px;
  background-color: #FFFFFF;
  color: #141630;
  text-align: center;
}
@media (min-width: 769px) {
  .footer__main {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    padding: 60px 70px;
    text-align: left;
  }
}
.footer__logo {
  margin: 0 auto 30px;
  padding: 0;
  width: 97px;
  height: 90px;
}
@media (min-width: 769px) {
  .footer__logo {
    margin: 0 40px 0 0;
    width: 115px;
    height: 115px;
  }
}
.footer__links {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer__item {
  margin: 0 0 20px;
  text-align: center;
}
@media (min-width: 769px) {
  .footer__item {
    margin: 0 0 10px;
    text-align: left;
  }
}
.footer__item:last-of-type {
  margin-bottom: 0;
}
.footer__link {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 19px;
  color: #141630;
}
.footer__link:hover {
  color: #EF7D3F;
}
.footer__contact {
  margin: 40px 0 0;
}
@media (min-width: 769px) {
  .footer__contact {
    margin-top: 0;
    margin-left: auto;
  }
}
.footer__social {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  gap: 27px;
  margin: 22px 0;
}
@media (min-width: 769px) {
  .footer__social {
    justify-content: start;
    gap: 10px;
  }
}
.footer__bottom {
  padding: 20px;
}
.footer__copyright {
  margin: 0;
  padding: 0;
  text-align: center;
  color: #FFFFFF;
  font-size: 12px;
  line-height: 14px;
}

.btn__icon {
  display: inline-block;
  margin: 0;
  padding: 0;
  width: 34px;
  height: 34px;
  border: none;
  background-color: transparent;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  transition: transform 0.2s ease-in-out;
}
.btn__icon:hover {
  transform: scale(1.2);
}
.btn__icon--info {
  background-image: url("../media/class_details_i_icon-ic_info.png");
  background-image: url("../media/class_details_i_icon-ic_info.svg");
  width: 17px;
  height: 17px;
}
.btn__icon--cart {
  background-image: url("../media/home_cart_icon_circle.png");
  background-image: url("../media/home_cart_icon_circle.svg");
}
.btn__icon--facebook {
  background-image: url("../media/footer_facebook_icon.png");
  background-image: url("../media/footer_facebook_icon.svg");
}
.btn__icon--instagram {
  background-image: url("../media/footer_instagram_icon.png");
  background-image: url("../media/footer_instagram_icon.svg");
}
.btn__icon--twitter {
  background-image: url("../media/footer_twitter_icon.png");
  background-image: url("../media/footer_twitter_icon.svg");
}

.btn {
  height: 40px;
  margin: 0;
  padding: 10px 50px;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
  border: none;
  border-radius: 20px;
}
.btn-primary {
  background: linear-gradient(0deg, #E15B11 0%, #EB9845 54%, #EF7D3F 100%);
  transition: transform 0.3s ease-in-out;
}
.btn-primary:hover {
  transform: scale(1.05);
}
.btn-mobile {
  background: linear-gradient(0deg, #E15B11 0%, #EB9845 54%, #EF7D3F 100%);
  width: 187px;
  height: 52px;
  padding: 18px 26px;
  border-radius: 26px;
}
.btn-outline {
  width: 110px;
  height: 28px;
  padding: 7px;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.62px;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
  border-radius: 24px;
}
.btn-outline:hover {
  background-color: #FFFFFF;
  color: #141630;
}
.btn-lg {
  height: 64px;
  padding: 20px 100px;
  font-size: 20px;
  line-height: 24px;
  border-radius: 64px;
}

.tag {
  display: inline-block;
  margin: 0;
  padding: 5px 15px;
  background-color: #FFFFFF;
  color: #141630;
  font-size: 14px;
  line-height: 16px;
  height: 26px;
  border-radius: 13px;
}
.tag--blue {
  background-color: #141630;
  color: #FFFFFF;
}

.breadcrumb {
  display: none;
  margin: 0 0 20px;
  padding: 0;
}
@media (min-width: 769px) {
  .breadcrumb {
    display: flex;
    align-items: center;
  }
}
.breadcrumb .breadcrumb-item {
  letter-spacing: 0.72px;
}
.breadcrumb .breadcrumb-item:before {
  color: #FFFFFF;
}
.breadcrumb .breadcrumb-item a {
  font-weight: 300;
  color: #FFFFFF;
}
.breadcrumb .breadcrumb-item a:hover {
  color: #EF7D3F;
}
.breadcrumb .breadcrumb-item .logo {
  width: 27px;
  height: 27px;
}

.snapshot {
  margin: 0;
  padding: 0;
  background: #FDFDFD;
  color: #141630;
  border-radius: 6px;
  overflow: hidden;
}
.snapshot__top {
  position: relative;
}
.snapshot__length {
  position: absolute;
  z-index: 2;
  right: 16px;
  bottom: 16px;
  background-color: #FFFFFF;
  color: #141630;
  padding: 5px 12px;
  border-radius: 12.5px;
  font-size: 12px;
  line-height: 14px;
}
@media (min-width: 769px) {
  .snapshot__length {
    font-size: 14px;
    line-height: 16px;
  }
}
.snapshot__content {
  padding: 33px;
}
.snapshot__title {
  margin: 0 0 12px;
}
.snapshot__underline {
  text-decoration: underline;
}
.snapshot__description {
  margin: 0 0 16px;
}
.snapshot__bottom {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 769px) {
  .snapshot__bottom {
    justify-content: flex-end;
    gap: 20px;
  }
}
.snapshot__price {
  margin: 0;
  padding: 0;
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 18px;
  line-height: 21px;
}

.class {
  margin: 50px 0 80px;
}
@media (min-width: 769px) {
  .class {
    margin: 24px 0 100px;
  }
}
.class__summary--desktop {
  display: none;
  padding: 60px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.4);
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
}
@media (min-width: 1024px) {
  .class__summary--desktop {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
  }
}
.class__summary--mobile {
  display: block;
}
@media (min-width: 1024px) {
  .class__summary--mobile {
    display: none;
  }
}
.class__left {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  gap: 32px;
}
.class__right {
  margin-left: auto;
}
.class__mobile-top {
  display: flex;
  flex-flow: row nowrap;
  margin-bottom: 24px;
}
.class__mobile-sell {
  margin-left: auto;
}
.class__coach {
  margin: 15px 0;
  font-size: 20px;
  line-height: 24px;
}
.class__length {
  margin: 0 0 20px;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.8);
}
.class__sell {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;
  gap: 22px;
}
.class__price {
  margin: 0 0 28px;
  font-family: "Hanken Grotesk", sans-serif;
  font-size: 20px;
  line-height: 24px;
  text-align: right;
}
@media (min-width: 1024px) {
  .class__price {
    margin: 15px;
  }
}
.class__buttons {
  margin-top: 80px;
}
.class__image {
  width: 104px;
  height: 104px;
}
@media (min-width: 1024px) {
  .class__image {
    width: 157px;
    height: 157px;
  }
}
.class__snapshots {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  gap: 12px;
}
.class__content {
  margin: 50px 0;
}
@media (min-width: 1024px) {
  .class__content {
    display: flex;
    flex-flow: row nowrap;
    gap: 20px;
  }
}
.class__video {
  margin: 0 0 50px;
  border-radius: 6px;
  overflow: hidden;
}
@media (min-width: 1024px) {
  .class__video {
    margin: 0;
  }
}
.class__video-placeholder {
  position: relative;
}
.class__video-placeholder:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.4);
}
.class__video-placeholder:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  background-image: url("../media/mobile_video_play_button.png");
  background-image: url("../media/mobile_video_play_button.svg");
  background-position: center;
  background-size: 59px;
  background-repeat: no-repeat;
}
.class__video-description {
  margin: 0;
  padding: 21px;
  background-color: rgba(255, 255, 255, 0.15);
}
@media (min-width: 1024px) {
  .class__lessons {
    min-width: 380px;
  }
}
