.class__lessons .accordion .accordion-item:not(:last-of-type) {
  border-bottom: 1px solid rgba(20, 22, 48, 0.4);
}
.class__lessons .accordion .accordion-item .accordion-button {
  border: none;
  padding: 32px;
  color: #141630;
}
.class__lessons .accordion .accordion-item .accordion-button--tag {
  padding: 32px 32px 12px;
}
.class__lessons .accordion .accordion-item .accordion-button:focus {
  outline: none;
  border: none;
  box-shadow: none;
}
.class__lessons .accordion .accordion-item .tag {
  margin: 0 32px 32px;
}
.class__lessons .accordion .accordion-item .accordion-body {
  padding: 0 32px 32px;
}
.class__lessons .accordion .accordion-item .accordion-body p {
  color: #141630;
