.class .coach {
  margin-top: 80px;
  padding: 26px;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 6px;
  overflow: hidden;
}
@media (min-width: 769px) {
  .class .coach {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    gap: 66px;
  }
}
@media (min-width: 1024px) {
  .class .coach {
    margin-top: 66px;
    padding: 22px 30px;
  }
}
.class .coach__profile {
  display: flex;
  flex-flow: row nowrap;
  gap: 54px;
  margin-bottom: 30px;
}
@media (min-width: 1024px) {
  .class .coach__profile {
    gap: 18px;
    margin: 0;
  }
}
.class .coach__photo {
  width: 87px;
  height: 87px;
}
.class .coach__info {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}
.class .coach__title {
  margin-bottom: 0;
  text-transform: uppercase;
  opacity: 0.8;
}
.class .coach__description {
  margin: 0;
  padding: 0;
  flex: 1;
  font-size: 14px;
  line-height: 16px;
  opacity: 0.6;
}

.cta {
  display: flex;
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
  margin-top: 98px;
  margin-bottom: 88px;
  background-image: url("../media/ready_to_go_image.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 769px) {
  .cta {
    background-image: url("../media/home_ready_to_go_image.jpg");
  }
}
.cta__container {
  width: 100%;
}
.cta__container::before {
  content: "";
  width: 1px;
  margin-left: -1px;
  float: left;
  height: 0;
  padding-top: 73.6714975845%;
}
@media (min-width: 769px) {
  .cta__container::before {
    padding-top: 20.2777777778%;
  }
}
.cta__container::after {
  content: "";
  display: table;
  clear: both;
}
.cta__inner {
